@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

/* Html, Body */
body {
  height: 100vh;
  display: grid;
  grid-template-rows: auto 0px;
  background-color: rgba(13, 12, 14, 0.95);
}

/* Main */
main {
  display: flex;
  flex-direction: column;
  padding: 30px 50px;
  border-radius: 10px;
  text-align: center;
  background-color: #323a49;
  max-width: 550px;
  width: fit-content;
  height: fit-content;
  margin: auto 20px;
  /* Media Query */
  margin: auto;
  /* Divider */
}
main .advice-container {
  /* Advice Id */
  /* Advice Text */
}
main .advice-container .advice-id {
  color: #52ffa8;
  text-transform: uppercase;
  word-spacing: 10px;
}
main .advice-container .advice-text {
  margin: 20px auto 0;
  font-size: 28px;
  width: 100%;
  color: hsl(193deg, 38%, 86%);
}
main .divider {
  margin: 20px 0 30px;
  background-size: 100% 100%;
  width: 100%;
}
main #dice {
  background-color: #52ffa8;
  border-radius: 50%;
  padding: 15px 16px 12px;
  border: 0;
  cursor: pointer;
  margin: 0 auto -50px;
}
main #dice img {
  width: 23px;
}
main #dice:hover {
  box-shadow: 0 0 40px #52ffa8;
}

/* Attribution */
.attribution {
  font-size: 0.8rem;
  align-self: flex-end;
  text-align: center;
  background-color: #323a49;
  padding: 10px;
  color: hsl(193deg, 38%, 86%);
}
.attribution .attribution a {
  color: #52ffa8;
}

